import EventBaseLayer from "../bases/EventBaseLayer";

let PixiInfo = {
    layerType: "Pixi",
    properties: {
        Pixi: {
            type: "pixi",
            defaultValue: "",
            blendOperation: "singleton"
        },
        "Attach To Eye": {
            type: "bool",
            defaultValue: false,
            blendOperation: "lastOneWins"
        },
        "Apply Eye Lighting": {
            type: "bool",
            defaultValue: false,
            blendOperation: "lastOneWins"
        }
    },
    dofs: {

    }
};

export default class PixiLayer extends EventBaseLayer {

    static getInfo() {
        return PixiInfo;
    }

    constructor() {
        super();
    }

    static generateEvent(props, layerNum) {

        let event = {
            name: "play-pixi",
            payload: {
                file: props.Pixi,
                layerNum,
                attach: props["Attach To Eye"],
                effect: props["Apply Eye Lighting"]
            }
        };

        return event;
    }

    static isValid(generatedEvent) {
        return ((generatedEvent.name === "play-pixi") &&
        (generatedEvent.payload.file.length > 0));

    }
}